	module.exports = {"data":{"markdownRemark":{"html":"<h1>Drifting Through An Opened Mind</h1>\n<p>Some time ago, I read about The Beatles' song writing techniques.\nAll of the techniques are both clever and sensible.</p>\n<h3>Here's my favorite</h3>\n<p>They didn't write down the tune or melody of new songs.\nThis was not out of laziness, but a way to filter out the unmemorable songs.\nThe post,\n<a href=\"http://www.telegraph.co.uk/culture/music/the-beatles/10321180/The-Beatles-the-birth-of-the-band.html\">The Beatles: the birth of the band</a>\ndescribes the technique:</p>\n<blockquote>\n<p>If they couldn't remember something the next day, they could hardly expect it to stick in the mind of anyone else</p>\n</blockquote>\n<p>The Beatles understood the importance of getting a song stuck in a listener's memory.\nThey didn't let their conscious judgement choose which songs would or wouldn't be catchy.\nInstead, they let their subconsciouses filter the dull songs right out of their mind.</p>\n<p>That idea stuck inside of my own mind.\nI tried applying the technique to things that I do.\nAfter some successful experimentation, I fully adopted the technique into my own workflow.</p>\n<h3>It's Not Just For Songwriting</h3>\n<p>The technique has proven to be great tool for songwriting.\nIt can't be denied that The Beatles wrote perhaps the most catchy songs of all time.\nHowever, this technique can be used to do much more than write songs.</p>\n<p>Generally speaking, it forces your focus on the most important task at hand.\nIn the creative mind, ideas drift in and drift out all day.\nSome thoughts keep coming up, over and over again, these are often the best ideas.</p>\n<p>There are many tasks which engage the creative side of the mind.\nWe can apply the technique in any of those situations.\nThe book\n<a href=\"http://www.amazon.com/gp/product/0307463745/ref=as_li_ss_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=0307463745&#x26;linkCode=as2&#x26;tag=richsonicom-20\">Rework</a>\nexplains an iteration of the same technique (unintentionally), but for creating software.\nBelow is a series of excerpts from the <em>Don't write it down</em> chapter of\n<a href=\"http://www.amazon.com/gp/product/0307463745/ref=as_li_ss_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=0307463745&#x26;linkCode=as2&#x26;tag=richsonicom-20\">Rework</a></p>\n<blockquote>\n<p> How should you keep track of what customers want? Don't.\nThe requests that really matter are the ones you'll hear over and over.\nIf there is a request you keep forgetting, that's a sign it isn't very important.</p>\n</blockquote>\n<p>Both the authors of <em>Rework</em>, and <em>The Beatles</em> applied the same technique with great success.\n<strong>If it's not good enough for you to remember, you customers certainly won't</strong>.</p>\n<h1>A Case Study</h1>\n<p >\n  This is an outline of how this technique fits within my workflow for writing a blog post:\n</p>\n<pre><code>-  Each day I take a 25 minute walk to the train\n-  I do whatever I want during that walk\n  -  Sometimes I listen to an audiobook\n  -  Other times I just listen to some music\n- While walking and listening, I am open to letting my mind wander\n- Sometimes new ideas occur, or old ideas bubble back to the surface\n- When I need to write a blog post, I pay attention to the recurring ideas\n- ...and, if I find an idea I like\n   - I draft it on the train that morning\n   - I try to get the whole idea out in that hour, unstructured\n   - After that I revise the post many times\n- Otherwise, I repeat this process until an idea comes up\n</code></pre>\n<p>When the brain is told to produce ideas, the subconscious will start to hunt for matches.\nLike a search engine, it may take time to load, but it usually has the results you need.\nIt returns mostly new versions and combinations of ideas it previously conceived.</p>\n<h3>Dont Believe Me?</h3>\n<p>If you are having trouble conjuring up ideas, then train your brain to get creative.\nRead more about the science of harnessing your creative side in\n<a href=\"http://www.amazon.com/gp/product/1934356050/ref=as_li_ss_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=1934356050&#x26;linkCode=as2&#x26;tag=richsonicom-20\">Pragmatic Thinking and Learning.</a>\nThis book also presents practical methods for balancing your left and right brain.\nWhen properly balanced it becomes an unstoppable force at formulating striking new ideas.</p>\n<h1>Gotchas</h1>\n<p>This technique may not be the right choice in certain situations.\nSometimes its required to complete EVERYTHING that is asked :/.</p>\n<h3>Take Heed In These Situations</h3>\n<ol>\n<li>Doing important chores, or administrative things</li>\n<li>Doing work for a boss</li>\n</ol>\n<p>Its critical to remember to pay the rent, or to call grandma on her birthday.\nCertain things need to be on a list, and cannot be forgotten.</p>\n<p>Even if keeping a backlog of tasks is necessary, still keep this technique in mind.\nUse it to decide what really needs to be on <em>The Todo List</em>.</p>\n<p>As an example, buying new sneakers may not need to make the list.\nWhen you walk past a shoe store you will probably remember to buy some.\nIf not, life will go on.\nDon't burden yourself by putting those tasks on your todo list.</p>\n<p>Bosses have sturdy expectations.\nThey don't appreciate only some work being completed.\nSometimes they think that mundane ideas, or worse, the wrong ideas are important.\nAs stupid as this is, they write the check, so do as they wish.</p>\n<h3>...Otherwise</h3>\n<p>Employ this method, and enjoy not having a massive laundry list of ideas and things to do.\nProjects shouldn't be bogged down with details that don't matter.\nThe authors of\n<a href=\"http://www.amazon.com/gp/product/0307463745/ref=as_li_ss_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=0307463745&#x26;linkCode=as2&#x26;tag=richsonicom-20\">Rework</a>\nsay: '<em>If there is a request you keep forgetting, that's a sign it isn't very important.</em>'\nSo, go out and enjoy the work you are doing, and don't sweat the small stuff!!!</p>\n<hr>\n<h1>Further Reading</h1>\n<ol>\n<li>The Young Beatles: <a href=\"http://www.telegraph.co.uk/culture/music/the-beatles/10321180/The-Beatles-the-birth-of-the-band.html\">The Beatles: the birth of the band</a></li>\n<li>Productivity, and running a company: <a href=\"http://www.amazon.com/gp/product/0307463745/ref=as_li_ss_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=0307463745&#x26;linkCode=as2&#x26;tag=richsonicom-20\">Rework</a></li>\n<li>Thinking and being creative: <a href=\"http://www.amazon.com/gp/product/1934356050/ref=as_li_ss_tl?ie=UTF8&#x26;camp=1789&#x26;creative=390957&#x26;creativeASIN=1934356050&#x26;linkCode=as2&#x26;tag=richsonicom-20\">Pragmatic Thinking and Learning.</a></li>\n</ol>","frontmatter":{"title":"When Creating Anything, Think Like The Beatles"},"fields":{"date":"2014-02-01"}}},"pathContext":{"slug":"/posts/2014-02-01-when-creating-anything-think-like-the-beatles/"}}

